import React, { useEffect, useState }  from 'react';
import axios from 'axios';
import { Container, Typography, Stack, Card, CardContent, CardActionArea, CardActions} from '@mui/material';
import { Box } from '@mui/system';
import { Link } from 'react-router-dom';
import styled from '@emotion/styled';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import PeopleAltRoundedIcon from '@mui/icons-material/PeopleAltRounded';

const StyledButton =  styled('button')({
  border: '1px solid rgb(22, 21, 27, 0.8)',
  backgroundColor: 'rgb(22, 21, 27, 0.8)', 
  paddingBottom: 8, 
  paddingTop: 8, 
  fontWeight: 700, 
  width: 80,
  color:'white',
  borderRadius:20,
  cursor:'pointer',
  '&:hover': {
    backgroundColor: 'rgb(22, 21, 27, 0.2)', 
  }
})


const Home = () => {
  var [recipes,setrecipes] = useState([]);
  
  useEffect(() => {
    fetchrecipes();
  }, [])
  const fetchrecipes = () => {
    axios.get("/api/getrecipes")
    .then((res) => {
      setrecipes(recipes = res.data);
    })
    
  }
  const removeMovie = (id) => {
    var movieId = {"_id":id};
    axios.post("/api/removerecipe", movieId)
    .then((res) => {
      alert("Removed Successfully!!")
      fetchrecipes();
    })
  }
  
  return (
    <div className='main-wrapper'>

      <Container maxWidth="lg" sx={{mt:4}}>
        <Box 
        sx={{ display: 'flex', justifyContent:'center', alignContent:'center', flexWrap: 'wrap', '& > :not(style)': { m: 1}, }}
        >
          {recipes.map((value, i) => (
            <Card elevation={24} key={i} sx={{ width: 190, p: 2}}>
              <CardActionArea>
                <img className='recipe-image' src={value.image} alt="recipe"/>
                <CardContent>
                  <Typography pb={1} variant="h6" align='center' sx={{color:'rgb(72,61,108)', fontWeight:700, textTransform: 'uppercase'}} component="div">
                    {value.titleName.trim()}
                  </Typography>
                 
                  <Stack direction="row" alignItems="center" spacing={1} sx={{justifyContent:"center", alignContent:"center"}}>
                    <AccessTimeIcon />
                    <Typography sx={{textTransform:'capitalize'}} variant="body2" pr={4}>{value.duration}</Typography>
                    <PeopleAltRoundedIcon />
                    <Typography sx={{textTransform:'capitalize'}} variant="body2">{value.servings}</Typography>
                  </Stack>
                </CardContent>
               
              </CardActionArea>
              <CardActions sx={{justifyContent:'center'}}>
                <Stack direction="row"  alignItems="center"spacing={2}>
                  <StyledButton> <Link className='edit-link' to={`/updatemovie/${value._id}`}>Edit</Link> </StyledButton>
                  <StyledButton onClick={()=>{removeMovie(value._id)}}>Remove</StyledButton>
                </Stack>
              </CardActions>
            </Card>
        ))}
        </Box>
      </Container>
    </div>
  )
}

export default Home