import React, { useState } from 'react'
import { Avatar, Container, Typography, Box, Grid, TextField, CssBaseline, Button } from '@mui/material';
import FoodBankRoundedIcon from '@mui/icons-material/FoodBankRounded';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';


const AddRecipe = () => {
  const [recipeInfo, setrecipe] = useState({"titleName":"","duration":0,"servings":0,"image":""});
  var [formErrors, setformErrors] = useState({"titleName":"","duration":"","servings":"","image":""});
  const numberRegex = /^[0-9]*$/;
  const urlRegex = /((([A-Za-z]{3,9}:(?:\/\/)?)(?:[\-;:&=\+\$,\w]+@)?[A-Za-z0-9\.\-]+|(?:www\.|[\-;:&=\+\$,\w]+@)[A-Za-z0-9\.\-]+)((?:\/[\+~%\/\.\w\-_]*)?\??(?:[\-\+=&;%@\.\w_]*)#?(?:[\.\!\/\\\w]*))?)/;
  const navigate = useNavigate();


  const validateForm = (e) =>{
    e.preventDefault();
    var isFormValid = true;
    var errors ={};
  
    if (!recipeInfo.titleName) {
      errors.titleName ='Recipe Name Required!';
      isFormValid = false;
    } 
    if (!recipeInfo.duration) {
      errors.duration ='Recipe Duration Required!';
      isFormValid = false;
    }  else if (!numberRegex.test(recipeInfo.duration)) {
      errors.duration = 'Invalid number!!';
      isFormValid = false;
    }
    if (!recipeInfo.servings) {
      errors.servings ='Number of Servings Required!';
      isFormValid = false;
    } else if (!numberRegex.test(recipeInfo.servings)) {
      errors.servings = 'Invalid number!!';
      isFormValid = false;
    }
    if (!recipeInfo.image) {
      errors.image ='Image Url required!';
      isFormValid = false;
    }  else if (!urlRegex.test(recipeInfo.image)) {
      errors.image ='Invalid Url!';
      isFormValid = false;
    }
    
    if(!isFormValid) {
      setformErrors(errors);
    } else {
      submitrecipeInfo();
    }
  }

  const submitrecipeInfo = () =>{
    axios.post("/api/addrecipe", recipeInfo)
    .then((res) => {
      console.log(res.data);
      if(res.data.status) {
        alert("Recipe Added Successfully!!");
        setTimeout(() => {
          navigate("/");         
        }, 700);
      } else {
        alert("Error Occured!!");
      }
    })
  }

  const onValueChange = (e) => {
    setrecipe({...recipeInfo, [e.target.name]: e.target.value})
  }

  return (
    <div className='main-wrapper'>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', }} >
          <Avatar sx={{ m: 1, bgcolor: 'rgb(22, 21, 27)' }}>
            <FoodBankRoundedIcon />
          </Avatar>
         
          <Typography component="h1" variant="h5">
            Add Recipe
          </Typography>
          <Box component="form" onSubmit={validateForm} sx={{ mt: 3 }}>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <Typography className='form-required' sx={{mb:1, color:'black'}} component="p" variant="p">Recipe Name</Typography>
                <TextField onChange={onValueChange} value={recipeInfo.titleName} name="titleName" 
                  autoComplete="titleName"  fullWidth autoFocus
                />
                {formErrors.titleName ? 
                  <Typography sx={{ mt:1, fontSize:10, pl:1, mb:1, color:'red'}} component="span" variant="span">
                    {formErrors.titleName}
                  </Typography> 
                : ''}
              </Grid>
              <Grid item xs={12}>
                <Typography className='form-required' sx={{mb:1, color:'black'}} component="p" variant="p">Cooking Duration (min)</Typography>
                <TextField onChange={onValueChange} value={recipeInfo.duration} name="duration" 
                  fullWidth autoComplete="duration" 
                />
                {formErrors.duration ? 
                  <Typography sx={{ mt:1, fontSize:10, pl:1, mb:1, color:'red'}} component="span" 
                    variant="span">{formErrors.duration}
                  </Typography> 
                : '' }
              </Grid>
              <Grid item xs={12}>
                <Typography className='form-required' sx={{mb:1, color:'black'}} component="p" variant="p">Serving Number</Typography>
                <TextField onChange={onValueChange} value={recipeInfo.servings} name="servings" 
                  fullWidth autoComplete="servings" 
                />
                {formErrors.servings ? 
                  <Typography sx={{ mt:1, fontSize:10, pl:1, mb:1, color:'red'}} component="span" 
                    variant="span">{formErrors.servings}
                  </Typography> 
                : '' }
              </Grid>
             
              <Grid item xs={12}>
                <Typography className='form-required' sx={{mb:1, color:'black'}} component="p" variant="p">Image Url</Typography>
                <TextField onChange={onValueChange} value={recipeInfo.image} name="image" 
                  fullWidth autoComplete="image" 
                />
                {formErrors.image ? 
                  <Typography sx={{ mt:1, fontSize:10, pl:1, mb:1, color:'red'}} component="span" 
                    variant="span">{formErrors.image}
                  </Typography> 
                : '' }
              </Grid>
             
            </Grid>
            <Button type="submit" fullWidth variant="contained" 
              sx={{ mt: 3, mb: 2, 
                bgcolor: 'rgb(22, 21, 27, 0.8)', 
                pb: 2, pt: 2, fontWeight: '700',
                '&:hover': {backgroundColor: '#fff',color: 'rgb(22, 21, 27, 0.8)',
              }}} >
              Add
            </Button>
          </Box>
        </Box>
      </Container>
    </div>
  )
}

export default AddRecipe